.react-tagsinput {
  background-color: #fff;
  border: 1px solid #8D8D8D;
  overflow: hidden;
  padding-left: 5px;
  padding-top: 5px;
}

.react-tagsinput--focused {
  border-color: #8D8D8D;
  box-shadow: 1px 1px 2px 2px #8D8D8D;
}

.react-tagsinput-tag {
  background-color: #CCEEBB;
  border-radius: 2px;
  border: 1px solid #CCEEBB;
  color: #232323;
  display: inline-block;
  font-family: sans-serif;
  font-size: 13px;
  font-weight: 400;
  margin-bottom: 5px;
  margin-right: 5px;
  padding: 5px;
}

.react-tagsinput-remove {
  cursor: pointer;
  font-weight: bold;
}

.react-tagsinput-tag a::before {
  content: " Ã—";
}

.react-tagsinput-input {
  background: transparent;
  border: 0;
  color: #232323;
  font-family: sans-serif;
  font-size: 13px;
  font-weight: 400;
  margin-bottom: 6px;
  margin-top: 1px;
  outline: none;
  padding: 5px;
  width: 80px;
}